html {
	font-size: 10px;
}

* {
	box-sizing: border-box;
}

body {
	margin: 0;
	padding: 0;
	background-color: rgb(244, 244, 244);
	color: #333;
}

p {
	font-size: 1.6rem;
}

small {
	font-size: 1.2rem;
}
.title {
	text-align: center;
	font-size: 3rem;
	color: rgb(46, 6, 6);
}
form {
	text-align: center;
}

input {
	font-size: 1.6rem;
	padding: 0.5rem 2rem;
	line-height: 2.8rem;
	border-radius: 20px;
	border: 1px solid #ddd;
	margin-bottom: 1rem;
}

button {
	background-color: rgba(0, 0, 0, 0.75);
	color: white;
	padding: 1rem 2rem;
	border: 1px solid rgba(0, 0, 0, 0.75);
	border-radius: 20px;
	font-size: 1.4rem;
	cursor: pointer;
	transition: background-color 250ms;
}

button:hover {
	background-color: rgba(0, 0, 0, 0.85);
}
